import React, { useState, useEffect } from 'react';
import {  BrowserRouter as Router, Route, Link, Routes  } from "react-router-dom";
import { Button, Tab, Tabs, Unstable_Grid2 as Grid,Typography } from '@mui/material';
import { darken, lighten } from '@mui/system';


import TwitterIcon from '@mui/icons-material/Twitter';
import PolicyIcon from '@mui/icons-material/Policy';


import { CardListTop } from './CardListTop';
import { DeckListTop } from './DeckListTop';
import { HideOnScrollAppBar } from './CustomAppBar';
import { LinkButton } from './CustomButton';


const Card = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(0);
  }, [setTabValue]);
  return <CardListTop />;
};

const Deck = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(1);
  }, [setTabValue]);
  return (
    <Button variant="contained" color="primary" component={Link} to="/cardlist">
  
</Button>

  );
};

const OnePersonTool = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(2);
  }, [setTabValue]);
  return <div></div>;
};

