export const Top = () => {
  const [value, setValue] = useState(0)

  return (
    <Router basename="/conan-cardgame"> {/* URL */}
      <HideOnScrollAppBar>
        <Grid container>
          <Grid>
            <Tabs value={value} onChange={(event, newValue) => setValue(newValue)}>
              <Tab label="" component={Link} to="/cardlist" />
              {/* <Tab label="" component={Link} to="/decklist" />
              <Tab label="" component={Link} to="/simulator" /> */}
            </Tabs>
          </Grid>
        </Grid>
      </HideOnScrollAppBar>
      <Routes>
        <Route path="/" element={<Card setTabValue={setValue} />} />
        <Route path="/cardlist" element={<Card setTabValue={setValue} />} />
        <Route path="/decklist" element={<Deck setTabValue={setValue}/>} />
        <Route path="/simulator" element={<OnePersonTool setTabValue={setValue} />} />
      </Routes>
      <Footer /> 
    </Router>
  );
};
